import React from "react";
import {connect} from "react-redux";
import {getSelectedCategory, getActualFilters, getSelectedPrice, getSelectedFilter, getSelectedNumberFilter, getProductListWrapper, getMatchToDataList, getIsSalesOfGoodsPage, getProductsTrees, getProductListData, getFilterConfiguration} from "../selectors";
import {getFilters, changeFilterAttribute, changeFilterNumberAttribute, getFilteredProductList, getFilteredProductTree, changePriceFilter,changeStickerFilter, getMatchToData, setSelectedModel, setSelectedRecomendedProducer, clearMatchToFilters, reloadMatchToFilter} from "eshop/modules/simfree/actions/filter";
import ProductFilterView from "eshop/modules/simfree/components/ProductFilterView";

const mapStateToProps = (state, ownProps) => ({
    selectedCategory: getSelectedCategory(state),
    filters: getActualFilters(state),
    selectedPrice: getSelectedPrice(state),
    selectedFilters: getSelectedFilter(state),
    matchToData: getMatchToDataList(state),
    products: getProductListWrapper(state),
    selectedNumberFilters: getSelectedNumberFilter(state),
    isSalesOfGoodsPage: getIsSalesOfGoodsPage(state),
    queryParams: ownProps.queryParams,
    isModal: false
});

const mapDispatchToProps = (dispatch) => ({
    reloadTree:(page) => dispatch(getFilteredProductTree(page)),
    getFilters: (selectedCategory) => dispatch(getFilters(selectedCategory)),
    changeFilterAttribute: (element, parent, multiValue, operation) => dispatch(changeFilterAttribute(element, parent, multiValue, operation)),
    changeFilterNumberAttribute: (type, value, element) => dispatch(changeFilterNumberAttribute(type, value, element)),
    changePriceFilter: (type, price) => dispatch(changePriceFilter(type, price)),
    changeStickerFilter: (value) => dispatch(changeStickerFilter(value)),
    getMatchToData: () => dispatch(getMatchToData()),
    setSelectedModel: (selectedModel, checked) => dispatch(setSelectedModel(selectedModel, checked)),
    setSelectedRecomendedProducer: (selectedProducer, checked) => dispatch(setSelectedRecomendedProducer(selectedProducer, checked)),
    getFilteredList:(page) => dispatch(getFilteredProductList(page)),
    clearMatchToFilters:() => dispatch(clearMatchToFilters()),
    reloadMatchToFilter: () => dispatch(reloadMatchToFilter())
});

const ProductFilterComponent = connect(mapStateToProps, mapDispatchToProps)(ProductFilterView);
export default ProductFilterComponent;

